<template>
    <div class="app">
        <div class="searchBox">
            <div class="searchInputBox" style="width: 100%;">
                <i></i>
                <input v-model="keyword" type="text" @blur="getList" placeholder="搜索">
            </div>
        </div>
        <ul class="curList">
            <li v-for="item in listData">
                <router-link v-if="item.state == 1" :to="{path:redirect ? redirect :　'/',　query: {currencyId: item.id}}">
                    <img :src="item.icon" alt="">
                    <p>
                        <span>{{item.name}}</span>
                        <span>{{item.tag}}</span>
                    </p>
                    <span class="curNum">{{item.balance}}</span>
                </router-link>
                <a v-else href="javascript:void (0)" @click="showAlert">
                    <img :src="item.icon" alt="">
                    <p>
                        <span>{{item.name}}</span>
                        <span>{{item.tag}}</span>
                    </p>
                    <span class="curNum">{{item.balance}}</span>
                </a>
            </li>
        </ul>
        <router-link to="/cooperation" class="cooperation" href=" ">
            <p><span>项目方代币上线申请</span></p >
        </router-link>
    </div>
</template>

<script>
    import { currencyList } from "@/api/index"
    import Dialog from 'vant/lib/dialog';
    import 'vant/lib/dialog/style';
    export default {
        name: "Wcurrency",
        data(){
            return {
                redirect:'',
                keyword:'',//搜索关键字,
                listData:[] // 币种列表
            }
        },
        mounted()
        {
            this.redirect = this.$route.query.redirect
           this.getList()
        },
        methods:{

            // 获取币种列表
            getList(){

                currencyList(this.keyword).then(res => {

                    this.listData = res
                })
            } ,
            showAlert()
            {
                Dialog.alert({
                    title: '系统提醒',
                    message: '当前币种暂未开放'
                }).then(() => {
                    // on close
                });
            }
        }
    }
</script>

<style scoped>
   @import "../../static/css/wcurrency.css";
</style>



